import {TOGGLE_DEBUG} from "../events.js";
import {eventLink} from "./event-link.js";
export function debugContainer(ctx, debug, json) {
  return [
    "div#debug",
    ctx.ui.column.debug[debug],
    [
      eventLink,
      [TOGGLE_DEBUG],
      ctx.ui.debugToggle,
      debug ? "close▼" : "open▲"
    ],
    ["pre", ctx.ui.code, json]
  ];
}
