import {button} from "./button.js";
import {RESET_KEY_STATE, SET_RANDOM_KEY_STATE} from "../events.js";
export const toolbar = (ctx) => {
  const ui = ctx.ui;
  const btClear = button((e) => {
    e.preventDefault();
    ctx.bus.dispatch([RESET_KEY_STATE]);
  }, "CLEAR", ui.toolbar.button);
  const btRandom = button((e) => {
    e.preventDefault();
    ctx.bus.dispatch([SET_RANDOM_KEY_STATE]);
  }, "RANDOM", ui.toolbar.button);
  return () => ["div", ui.toolbar.root, [btClear], [btRandom]];
};
