import {dropdown} from "../../_snowpack/pkg/@thi.ng/hdom-components.js";
import {transduce, map, push} from "../../_snowpack/pkg/@thi.ng/transducers.js";
import WebMidi from "../../_snowpack/pkg/webmidi.js";
import {TOGGLE_KEY_STATE, SET_MIDI_DEVICES} from "../events.js";
const bindAllDevices = (bus) => {
  WebMidi.inputs.forEach((input) => input.addListener("noteon", "all", (e) => {
    bus.dispatch([TOGGLE_KEY_STATE, e.note.number % 12]);
    console.log(`Received 'noteon' message (${e.note.name + e.note.octave}) from ${input.name}`);
  }));
};
export const isEnabledMidi = (bus) => {
  WebMidi.enable((err) => {
    if (err) {
      console.log("WebMidi could not be enabled.", err);
      return;
    } else {
      console.log("WebMidi enabled!");
      bus.dispatch([SET_MIDI_DEVICES, WebMidi.inputs.map((x) => x.name)]);
    }
    bindAllDevices(bus);
  });
};
const bindDevice = (bus, device) => {
  WebMidi.inputs.forEach((input) => input.removeListener());
  if (device === "All Devices") {
    bindAllDevices(bus);
    return;
  } else {
    const selectDevice = WebMidi.getInputByName(device);
    if (selectDevice) {
      selectDevice.addListener("noteon", "all", (e) => {
        bus.dispatch([TOGGLE_KEY_STATE, e.note.number % 12]);
        console.log(`Received 'noteon' message (${e.note.name + e.note.octave}) from ${device}`);
      });
    }
  }
};
export const midiDevicesStatus = (ctx) => {
  const views = ctx.views;
  const ui = ctx.ui.midiDevicesStatus;
  const bus = ctx.bus;
  return () => {
    const enabledMidi = views.enabledMidi.deref();
    let midiDevices = views.midiDevices.deref();
    return [
      "div",
      ["span", ui.content[Number(enabledMidi)], ""],
      [
        "span",
        ui.dropdown.root,
        midiDevices.length >= 1 ? [
          dropdown,
          {
            ...ui.dropdown.selection,
            ...{
              onchange: (e) => bindDevice(bus, e.target.value)
            }
          },
          transduce(map((x) => [x, x]), push(), ["All Devices", ...midiDevices])
        ] : "Web MIDI unavailable"
      ]
    ];
  };
};
